import React,{Component} from 'react';
import axios from 'axios';
import {withRouter} from 'react-router-dom'
const mealurl="https://devloperrestapi.herokuapp.com/restaurantList";

class Costfilter extends Component{
    
costfilter=(event)=>{
      console.log(event.target.value);  
    let costvalue=(event.target.value).split(',');
    let hcost=Number(costvalue[0]);
    let lcost=Number(costvalue[1]);
    let mealType=sessionStorage.getItem('mealid')
    let curl;
    if(costvalue ===''){
        curl=`${mealurl}/${mealType}`
    }
    else{
        curl=`${mealurl}/${mealType}?hcost=${lcost}&lcost=${hcost}`
    }
    axios.get(curl)
    .then((response)=>{this.props.costdata(response.data)})
       
}
      render(){
          return(
              <React.Fragment>
              <center>Cost filter</center>

              <div onChange={this.costfilter}>
              <label  className="radio">
              <input type="radio" value=""  name="cost" />All
              </label>
              <label>
              <input type="radio" value="100,300" name="cost"/>100-300
              </label>
              <label>
              <input type="radio" value="301,500" name="cost"/>100-300
              </label>
              <label>
              <input  type="radio" value="501,800" name="cost"/>500-600
              </label>
              </div>
         
              </React.Fragment>
          )
      }
    }

export default withRouter(Costfilter);