import React from 'react';
import {withRouter} from 'react-router-dom'
const ViewBooking=(props)=>{
    

    const renderHotel=({overdata})=>{
        console.log("functional>>>",(props.location.search).split('=')[1])
       
        if(overdata){
            return overdata.map((item)=>{
                return (
                <tr>
                <td>{item.order_id}</td>
                <td>{item.Rest_id}</td>
                <td>{item.name}</td>
                <td>{item.phone}</td>
                <td>{item.address}</td>
                <td>{item.nuumberofperson}</td>
                </tr>

             ) })
        }

    }
    return(
        <div className="container">
    
        <alert className="alert alert-success">
                {(props.location.search).split('=')[1]}
            </alert>
        <center><h3>Order deatils</h3></center>
        <table className="table table-responsive">
        <thead>
        <tr>
        <th>order_id</th>
        <th>Rest_id</th>
        <th>name</th>
        <th>phone</th>
        <th>address</th>
        <th>number of person</th>
        </tr></thead>
        <tbody>
        {renderHotel(props)}
        </tbody></table>
        </div>
    )
}


export default withRouter(ViewBooking);