import React ,{Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom'

const resturl="https://devloperrestapi.herokuapp.com/restaurantdetails";
const placeholder="https://devloperrestapi.herokuapp.com/placeholder";

class Placeholder extends Component{
    constructor(){
        super()
        this.state={
            order_id:Math.floor(Math.random()*10000),
            hotel_name:'',
            address:'',
            name:'',
            phone:'',
            }
        }
        handleonchangename=(event)=>{
            console.log(this.target.event.value);
            this.setState({name:event.target.value})
        }
        handleonchangenumber=(event)=>{
            console.log(this.target.event.value);
            this.setState({phone:event.target.value})

        }
        handleonchangeperson=(event)=>{
            console.log(this.target.event.value);
            this.setState({person:event.target.value})
        }
        handleSubmit=()=>{
            
            fetch(placeholder,{
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json',
                 },
                body:JSON.stringify(this.state)
            })
            .then((this.props.history.push('/Booking?meassge="success"')))
        }
            async componentDidMount(){
                let hotelid=this.props.match.params.id;
            
                let response=await axios.get(`${resturl}${hotelid}`);
               
               
                this.setState({hotel_name:response.data[0].name})
                

            }
    
   
render(){
return(
    <div className="conatiner">
    <div className="panel panel-info">
    <div className="panel panel-heading">
    <h4>Place your order</h4>
    </div> 
    <div className="panel panel-info">
    <div className="from-group">
    <label> order</label><input type="text" name="order_id" value={this.state.order_id} readOnly className="from-control"/>
    
    </div>
    <div className="from-group">
    <label> <input type="text" name="order_id" value={this.state.hotel_name} readOnly className="from-control"/>
    Restname</label>
    </div> 
    <div className="from-group">
    <label> Address </label> <input type="text" name="order_id" value={this.state.address}  className="from-control"/>
     </div> 
     <div className="from-group">
    <label>Phonenumber <input type="text" name="order_id" value={this.state.phone} onchange={this.handleonchangenumber} className="from-control"/>
     </label>
      </div>
     <div className="from-group">
     <label>Name</label> <input type="text" name="order_id" value={this.state.name} onchange={this.handlechangename} className="from-control"/>
      
     </div>
      <div className="from-group">
       <label><select className="" onchange={this.handlechangeperson}>
       <option>number of person</option>
       <option>1</option>
       <option>2</option>
       <option>3</option>
    </select>
    </label>
    </div>
<Link to={`/details/${this.props.match.params.id}`} className="btn btn-danger">Back</Link>
<Link  onClick={this.handleSubmit} className="btn btn-success">Submit</Link>
   </div>
    </div>
    </div>
        )}
}
 
 export default Placeholder;