import React from 'react';
import {Link} from 'react-router-dom';
import './Listing.css';
import Pagination from 'react-js-pagination';
const renderlist=(props,data)=>{
    if(data){
         var limit=props.limit;
         var page=props.activePage;
         data=data.slice((page-1)*limit,(page-1)*limit+limit);
       

         return data.map((item)=>{
             
             return(
               <div className="item" key={item._id}>
               <div className="row">
               
               <div className="col-md-5">
               <img className="imageclass" alt="" src={item.thumb}/>
               </div>
               <div className="col-md-6">
               <div className="hotel_name">
               <Link to={`/deatils/${item._id}`} className="hotel_name">{item.name}</Link>
               </div>
               </div>
            <div className="cityname">{item.city_name}</div>
               <div className="cityname">{item.localilty}</div>
               <div className="cityname">{item.address}</div>
               <div className="cityname">
               <span className="glyphicon glyphicon-glass"></span>
               <span className="glyphicon glyphicon-filter"></span></div>
               </div>
               <div className="row">
               <div className="col-md-3">
               <div className="ciuinse-block">Cuisine</div>
               <div className="cuisine-block">Cost per two</div>
               </div>
               <div className="cuisine-out">{item.Cuisine[0].name}|{item.Cuisine[1].name}</div>
               <div className="cuisine-out">{item.cost}</div></div>
               </div> 
               
         )}
         )
     }
     else{
         return (
             <img src='./image/loader.jpg' alt="" className="imageclass"></img>
         )
     }

 }

  const  handlePageChange=(props,pageNumber)=>{
        props.pageNumber(pageNumber)
        var data=props.listdata;
        renderlist(props,data)

    }
    const ListingDisplay=(props)=>{
        console.log(props)
  
    return(
        <div className="conatiner">
        
        <div className="col-md-16">
        {renderlist(props,props.listdata)}
        </div>
        <center>
        <Pagination
        activePage={props.activePage}
        itemsCountPerPage={props.limit}
        totalItemsCount={props.totalNoOfItems}
        pageRangeDisplayed={3}
        itemClass="page-item"
        linkClass="page-link"
        onChange={(pageNumber)=>{handlePageChange(props.pageNumber)}}
        />
        </center>
        </div>
    )

}



export default ListingDisplay;


