import React,{Component} from 'react';
import axios from 'axios';
import {withRouter} from 'react-router-dom'
const mealurl="https://devloperrestapi.herokuapp.com/restaurantList"

class Cuisinefilter extends Component{
    cuisinefilter=(event)=>{
    console.log(event.target.value);
    let cuisineType=event.target.value;
    let mealType=sessionStorage.getItem('mealid')
    let curl;
    if(cuisineType ===''){
        curl=`${mealurl}/${mealType}`
    }
    else{
        curl=`${mealurl}/${mealType}?cuisine=${cuisineType}`
    }
    axios.get(curl)
    .then((response)=>{this.props.cuisinedata(response.data)})
       
}
      render(){
          return(
              <React.Fragment>
              <center>cuisinefilter</center>
              <div  onChange={this.cuisinefilter}>
              <label  className="radio">
              <input type="radio" value=""  name="cuisine" />All
              </label>
              <label>
              <input type="radio" value="2" name="cuisine"/>South Indian
              </label>
              <label>
              <input type="radio" value="1" name="cuisine"/>North indian
              </label>
              <label>
              <input  type="radio" value="5" name="cuisine"/>Street food
              </label>
              <label>
              <input type="radio" value="4" name="cusisine"/>Fast food</label>
              <label>
              <input type="radio" value="3" name="cuisine"/>Chinese
              </label>
              </div>
            
              </React.Fragment>
          )
      }
}

export default withRouter(Cuisinefilter);