import React,{Component} from 'react';
import axios from 'axios';
import {withRouter} from 'react-router-dom'
import ListingDisplay from './ListingDisplay';
import Suggestion from './Suggestion';
import Costfilter from '../filters/costfilters';
import Cuisinefilter from '../filters/cuisinefilters'; 
import Sortfilter from '../filters/sortfilters'
const url="https://devloperrestapi.herokuapp.com/restaurents?mealtype=";
const murl="https://devloperrestapi.herokuapp.com/mealtype";
var limit=3;
class ListingApi extends Component{
    constructor(){
        super()
        this.state={
            hotelist:'',
            mealname:'',
            activePage:1,
            totalNoOfItems:1,
        }
    }
    setAsperFilter(sortdata){
        this.setState({hotelist:sortdata})
    }
    render(){
        return(
           <div className="conatiner">
                   <div className="row"> 
                       <div className="col-md-2">
                            <Costfilter costdata={(data)=>{this.setAsperFilter(data)}}/>
                            <Cuisinefilter cuisinedata={(data)=>{this.setAsperFilter(data)}}/>
                            <Sortfilter   sortdata={(data)=>{this.setAsperFilter(data)}}/>
                        </div>  
        
                        <center>
                         <h3>meal are here {this.state.mealname}</h3>

                        <Suggestion/>
                         </center>
                       <div className="col-md-10">
                       <center><h3>your options are there</h3></center>
                       <ListingDisplay listdata={this.state.hotelist} activePage={this.state.activePage}
                      totalNoOfItems={this.state.totalNoOfItems} limit={limit}
                       pageNumber={(data)=>{this.setState({activePaage:data})}}
                      ></ListingDisplay>
                       </div>
                    </div>
            </div>
        
)}
    
    componentDidMount(){
       var mealid=this.props.match.params.id
       sessionStorage.setItem('mealid',mealid)
       axios.get(`${murl}`)
       .then((response)=>{
           for(var i=0;i<response.data.length;i++){
               console.log("mealid",mealid)
               console.log("mealid",response.data[i]._id)
           if(response.data[i]._id===Number(mealid)){
                this.setState({mealname:(response.data[i].name).toUpperCase()})
                i=response.data.length;
           }
           else{
               this.setState({mealname:"this response is wrong"})
           }
         
        }
       })
       fetch(`${url}${mealid}`)
       .then((response)=> response.json()
       .then((data)=>{this.setState({
           hotelist:data.slice(0,data.length-1),
           totalnumberOfItems:data.length-1})}))
    }
}

export default withRouter(ListingApi);