import React,{Component} from 'react'
import axios from 'axios';
import ViewBooking from './Bookingdisplay';
const url="https://devloperrestapi.herokuapp.com/orders";
 class Booking extends Component{
     constructor(){
         super()
         this.state={
                order:''
         }
     }
     render(){
         return(
            <div className="conatiner">
            <ViewBooking orderdata={this.state.order}></ViewBooking>
            </div>
          )}
componentDidMount(){
     axios.get(`${url}`)
     .then((response)=>{this.setState({order:response.data[0]})})
     
}   
}
    
export default Booking;